<template>
  <div class="proAppAdd">
    <div id="viewDiv" ref="viewDiv" :class="{cursorPen:drawing && (drawType === 'polyline' || drawType === 'polygon') }"></div>
    <div class="addForm">
      <h3>项目信息</h3>
      <hr style="margin-bottom: 15px">
      <div>
        <Form ref="formValidate" :model="formItem" :rules="ruleValidate" :label-width="90">
          <FormItem label="项目名称" prop="name" style="margin-bottom: 18px">
            <Input v-model.trim="formItem.name" placeholder="请输入项目名称"  size="small"></Input>
          </FormItem>
          <FormItem label="项目时间" prop="time" style="margin-bottom: 18px">
            <DatePicker :editable="false" type="date" v-model.trim="formItem.time" placement="bottom-end" placeholder="请选择项目时间" style="width: 100%" size="small"></DatePicker>
          </FormItem>
          <FormItem label="属性" prop="attribute" style="margin-bottom: 18px">
            <Input v-model.trim="formItem.attribute" placeholder="请输入属性"  size="small"></Input>
          </FormItem>
          <FormItem label="类型" prop="type" style="margin-bottom: 18px">
            <Input v-model.trim="formItem.type" placeholder="请输入类型"  size="small"></Input>
          </FormItem>
          <FormItem label="缓冲区半径" prop="radius" style="margin-bottom: 18px">
            <Input v-model.trim="formItem.radius" placeholder="请输入缓冲区半径"  size="small" style="width: 80%"></Input>
            <span style="padding-left:10px">米</span>
          </FormItem>
          <FormItem label="项目描述" style="margin-bottom: 18px">
            <Input v-model.trim="formItem.description" type="textarea" placeholder="请输入项目描述"  :autosize="{maxRows: 3,minRows: 3}"/>
          </FormItem>
          <FormItem label="图层选择" style="margin-bottom: 18px">
            <Select v-model="layers" size="small" style="width:100%" >
              <Option value="redline">红线斑块</Option>
              <Option value="natural" disabled>自然保护区</Option>
            </Select>
          </FormItem>
          <FormItem label="项目形状" style="margin-bottom: 18px">
            <ButtonGroup style="width: 100%">
              <Button size="small" :type="drawType === 'point' ?  'primary' : 'default'" @click="drawType = 'point'" style="width: 33%">
                <Icon type="md-radio-button-on" />
              </Button>
              <Button size="small" :type="drawType === 'polyline' ?  'primary' : 'default'"  @click="drawType = 'polyline'" style="width: 33%">
                <Icon type="md-remove" />
              </Button>
              <Button size="small" :type="drawType === 'polygon' ?  'primary' : 'default'"  @click="drawType = 'polygon'" style="width: 33%">
                <Icon type="md-square" />
              </Button>
            </ButtonGroup>
          </FormItem>
          <FormItem :label-width="0" style="margin-bottom: 5px;width: 100%">
            <ButtonGroup style="width: 100%;" size="small">
              <Button @click="clickDraw" :type="drawing === true ?  'primary' : 'default'"   style="width: 50%">绘制</Button>
              <Button style="width: 50%">
                <Upload
                  :show-upload-list="false"
                  :format="['.xls']"
                  :before-upload="handleUpload"
                  action="#"
                  style="height: 19px">
                  <span style="display: inline-block;width:119px;">上传</span>
                </Upload>
              </Button>
            </ButtonGroup>
            <p style="text-align: right">
              <a href="/epr/excelModel/坐标.xls" download="坐标模板.xls"><span style="cursor: pointer;color:dodgerblue;">下载上传文件模板</span></a>
            </p>
            <p style="height:18px;color:red;text-align: right;" v-if = "drawValidate">
              <span>{{validateText}}</span>
            </p>
          </FormItem>
          <FormItem style="text-align: right;margin-bottom: 0">
            <Button ref="analysis" type="primary" size="small" @click="analysis" style="margin-right: 15px">分析</Button>
            <Button type="primary" size="small" @click="addPro" style="margin-right: 15px" :disabled="!analysisEnd">保存</Button>
            <Button type="primary" size="small" @click="$emit('signOut',false);analysisEnd = false">退出</Button>
          </FormItem>
        </Form>
      </div>
    </div>
    <div class="analysisList" :class="{ active : analysisList }">
      <ProAppAnalysis :data = "analysisData" :saveEnd="saveEnd" :shapearea="shapearea" @onclickDingwei="onclickDingwei" :mapPhoto="dataUrl"></ProAppAnalysis>
    </div>
    <Spin fix style="background: rgba(255, 255, 255, 0.5);font-size: 18px;color:#3B4EF5" size="large" v-if="analysisSpin">
      <Icon type="ios-loading" size=22 class="demo-spin-icon-load"></Icon>
      <div>分析计算中...</div>
    </Spin>
  </div>
</template>
<script>
import { importFile, addPro, getProjectById } from '@/api/projectApproval'
import arcgisPackage from '../../utils/arcgisTools'
import config from '../../setting/mapConfig'
import { tdtlayer, tdtyNoteslayer, redLine } from '@/utils/layers'
import ProAppAnalysis from './ProApp_analysis'
import boundaryMarker from '../../assets/projectApproval_images/position.png'
import { renderTime } from '@/utils/common'
export default {
  name: 'ProAppAdd',
  data () {
    return {
      shapearea: 0,
      id: '', // 数据id
      formItem: {}, // 表单数据
      layers: 'redline', // 分析图层选择
      drawType: 'point', // 项目形状
      shapeObj: {}, // 绘制的形状对象
      analysisList: false, // 分析列表状态
      drawing: false, // 是否正在绘制
      map: {}, // 地图实例
      view: {}, // 地图视图实例
      draw: {}, // 绘制的实例
      bufferLayer: {}, // 缓冲区图层
      insercLayer: {}, // 缓冲区相交图层
      srldFeature: [], // 相交红线数据
      ruleValidate: { // 表单验证规则
        name: [
          { required: true, message: '项目名称不能为空', trigger: 'change' }
        ],
        radius: [
          { required: true, type: 'string', message: '缓冲区半径不能为空', trigger: 'change' }
        ],
        attribute: [
          { required: true, type: 'string', message: '属性不能为空', trigger: 'blue' }
        ],
        type: [
          { required: true, type: 'string', message: '类型不能为空', trigger: 'blur' }
        ]
      },
      drawValidate: false, // 绘制状态验证
      validateText: '', // 绘制验证提示文本状态
      buffer: {}, // 缓冲区
      comState: false, // 缓冲区生成状态
      file: null,
      loadingStatus: false,
      excalData: [], // 上传的excal中的坐标数组
      analysisEnd: false, // 是否完成分析
      saveEnd: false, // 是否完成保存
      analysisSpin: false, // 分析时加载中动画状态
      dataUrl: '', // 截图base64图片
      saveFileUrl: '', // 保存后返回的分析报告路径
      proportion: '1', // 截图长宽比  height/width
      graphicUp: {}
    }
  },
  computed: {
    // 相交结果
    intersect: function () {
      if (this.srldFeature.length === 0) {
        return '不落在'
      } else {
        return '落在'
      }
    },
    // 相交明细
    analysisArr: function () {
      let arr = []
      for (let i in this.srldFeature) {
        let obj = {
          redineId: this.srldFeature[i].attributes.hxcode_1,
          intersectArea: (this.srldFeature[i].attributes.shapearea * 1).toFixed(2)
        }
        arr.push(obj)
      }
      return arr
    },
    // 缓冲区面积
    analysisArea () {
      return (this.buffer.shapearea * 1).toFixed(2)
    },
    // 传输给分析列表的数据
    analysisData: function () {
      let data = {
        radius: this.formItem.radius + '米',
        area: this.analysisArea + '平方米',
        result: this.intersect,
        details: this.analysisArr,
        comState: this.comState,
        fileUrl: this.saveFileUrl === '' ? this.fileUrl : this.saveFileUrl,
        name: this.formItem.name
      }
      return data
    },
    // 绘制图形点坐标
    coordinate: function () {
      if (JSON.stringify(this.shapeObj) !== '{}') {
        switch (this.shapeObj.type) {
          case 'polygon':
            return this.shapeObj.rings[0]
          case 'polyline':
            return this.shapeObj.paths[0]
          case 'point':
            return [this.shapeObj.x, this.shapeObj.y]
        }
      }
      return []
    },
    // 保存数据
    addData: function () {
      let obj = {
        id: this.id * 1,
        createBy: this.$store.getters.getloginInfor().userId,
        projectName: this.formItem.name,
        shape: this.drawType,
        radius: this.formItem.radius * 1,
        result: this.intersect,
        bufferRange: this.shapearea.toString(),
        detail: this.analysisArr,
        coordinate: this.coordinate,
        image: this.dataUrl,
        proportion: this.proportion,
        description: this.formItem.description,
        // time: renderTime(this.formItem.time),
        time: this.formItem.time,
        attribute: this.formItem.attribute,
        type: this.formItem.type
      }
      return obj
    }
  },
  props: {
    seeId: {},
    fileUrl: {} // 分析报告文件路径
  },
  components: {
    ProAppAnalysis
  },
  watch: {
    // 项目形状改变监控
    drawType: function () {
      this.drawing = false
      this.shapeObj = {}
      this.view.graphics.removeAll()
      this.map.remove(this.bufferLayer)
      this.map.remove(this.insercLayer)
    },
    // 查看触发
    seeId: function () {
      this.getProjectById()
      this.id = this.seeId
    }
  },
  mounted () {
    // 加载地图
    this.createMap()
  },
  methods: {
    /**
     * @Description:初始化地图的函数
     * @author 张淼
     * @date 2019/5/5
     */
    async createMap () {
      // 加载arcgis地图的依赖
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let Draw = await arcgisPackage.Draw

      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer1 = await tdtyNoteslayer().then(res => { return res })
      // 省界
      // let layerProvince = await shengjie().then(res => { return res })
      // 红线斑块图层
      let layerRedLine = await redLine().then(res => { return res })

      let customBasemap = new Basemap({
        baseLayers: [tdtylayer, tdtylayer1, layerRedLine],
        title: 'Custom Basemap',
        id: 'myBasemap'
      })

      this.map = new Map({
        basemap: customBasemap
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        spatialReference: {
          wkid: config.mapWkid
        },
        zoom: 7
      })
      // 添加红线斑块
      // this.map.layers.add(layerRedLine)
      // 实例化绘制
      this.draw = new Draw({
        view: this.view
      })
    },
    /**
     * @Description:点击绘制按钮的函数
     * @author 张淼
     * @date 2019/5/5
     */
    clickDraw () {
      if (this.drawing) {
        this.drawing = false
        this.shapeObj = {}
        // 先清除地图上的点
        this.view.graphics.removeAll()
        // 创建绘制action
        this.action = this.draw.create(this.drawType)
      } else {
        this.shapeObj = {}
        this.map.remove(this.bufferLayer)
        this.map.remove(this.insercLayer)
        this.drawing = true
        if (this.drawing) {
          // 创建绘制action
          this.action = this.draw.create(this.drawType)
          switch (this.drawType) {
            case 'point':
              this.drawPoint(this.action)
              break
            case 'polyline':
              this.drawLine(this.action)
              break
            case 'polygon':
              this.drawPolygon(this.action)
              break
          }
        }
      }
    },
    /**
     * @Description:绘制点的函数
     * @author 张淼
     * @date 2019/5/5
     */
    async drawPoint (action) {
      // 进行绘制点
      action.on(['cursor-update', 'draw-complete'], evt => {
        this.renderPoint(evt.coordinates)
      })
      action.on(['draw-complete'], evt => {
        this.drawing = false
      })
    },
    /**
     *  渲染点函数
     */
    async renderPoint (data) {
      let Graphic = await arcgisPackage.Graphic
      // 先清除地图上的点
      this.view.graphics.removeAll()
      // 定义点
      let point = {
        type: 'point',
        longitude: data[0],
        latitude: data[1],
        spatialReference: this.view.spatialReference
      }
      let markerSymbol = {
        type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
        url: boundaryMarker,
        height: 20,
        width: 20,
        yoffset: 10,
        outline: { // autocasts as new SimpleLineSymbol()
          color: [255, 255, 255],
          width: 1
        }
      }
      let graphic = new Graphic({
        geometry: point,
        symbol: markerSymbol
      })
      // 添加点
      this.view.graphics.add(graphic)
      this.graphicUp = graphic
      this.shapeObj = point
    },
    /**
     * @Description: 绘制线函数
     * @author 张淼
     * @date 2019/5/5
     */
    async drawLine (action) {
      // 监听绘制过程的函数
      action.on(['vertex-add', 'vertex-remove', 'cursor-update', 'draw-complete'], evt => {
        this.renderLine(evt.vertices)
      })
      action.on(['draw-complete'], evt => {
        this.drawing = false
      })
    },
    /**
     *  渲染线函数
     */
    async renderLine (data) {
      let Graphic = await arcgisPackage.Graphic
      // 清空图层
      this.view.graphics.removeAll()
      let polyline = {
        type: 'polyline',
        paths: data,
        spatialReference: this.view.spatialReference
      }
      let graphic = new Graphic({
        geometry: polyline,
        symbol: {
          type: 'simple-line', // autocasts as new SimpleFillSymbol
          color: [51, 133, 255],
          width: 2,
          cap: 'round',
          join: 'round'
        }
      })
      // 将折线添加到地图上
      this.view.graphics.add(graphic)
      this.graphicUp = graphic
      this.shapeObj = polyline
      this.shapeObj.paths = [this.shapeObj.paths]
    },
    /**
     * @Description: 绘制多边形
     * @author 张淼
     * @date 2019/5/6
     */
    async drawPolygon (action) {
      // 定义绘制事件
      action.on(['vertex-add', 'vertex-remove', 'cursor-update', 'draw-complete'], evt => {
        this.renderPolygon(evt.vertices)
      })
      action.on(['draw-complete'], evt => {
        this.drawing = false
      })
    },
    /**
     *  渲染多边形
     */
    async renderPolygon (data) {
      let Graphic = await arcgisPackage.Graphic
      // 清除图层
      this.view.graphics.removeAll()

      // 绘制多边形
      let polygon = {
        type: 'polygon', // autocasts as Polygon
        rings: data,
        spatialReference: this.view.spatialReference
      }
      let graphic = new Graphic({
        geometry: polygon,
        symbol: {
          type: 'simple-fill', // autocasts as SimpleFillSymbol
          color: [51, 133, 255, 0.5],
          style: 'solid',
          outline: {
            color: [51, 133, 255, 0.8],
            width: 1
          }
        }
      })
      this.view.graphics.add(graphic)
      this.graphicUp = graphic
      this.shapeObj = polygon
      this.shapeObj.rings = [this.shapeObj.rings]
    },
    /**
     *  绘制缓冲区
     */
    async getbuffer () {
      let Graphic = await arcgisPackage.Graphic
      let GraphicLayer = await arcgisPackage.GraphicsLayer
      let geometryEngine = await arcgisPackage.geometryEngine
      let GeometryService = await arcgisPackage.GeometryService
      let BufferParameters = await arcgisPackage.BufferParameters
      let AreasAndLengthsParameters = await arcgisPackage.AreasAndLengthsParameters
      let polySym = {
        type: 'simple-fill', // autocasts as new SimpleFillSymbol()
        color: [140, 140, 222, 0.5],
        outline: {
          color: [140, 140, 222, 0.8],
          width: 1
        }
      }
      let geometryService = new GeometryService({
        url: config.geometryServerUrl
      })
      /* 给shapeobj包装成geometry */
      if (this.shapeObj.type === 'point') {
        let Point = await arcgisPackage.Point
        this.shapeObj = new Point(this.shapeObj)
      } else if (this.shapeObj.type === 'polyline') {
        let Polyline = await arcgisPackage.Polyline
        this.shapeObj = new Polyline(this.shapeObj)
      } else if (this.shapeObj.type === 'polygon') {
        let Polygon = await arcgisPackage.Polygon
        this.shapeObj = new Polygon(this.shapeObj)
      }
      let bufferLayer = new GraphicLayer()
      if (this.view.spatialReference.isWGS84) {
        this.buffer = await geometryEngine.geodesicBuffer(this.shapeObj, this.formItem.radius, 'meters', false)
        this.buffer.shapearea = geometryEngine.geodesicArea(this.buffer, 'square-meters')
      } else {
        this.buffer = await geometryService.buffer(new BufferParameters({
          unit: 'meters',
          outSpatialReference: this.view.spatialReference,
          geometries: [this.shapeObj],
          distances: [parseInt(this.formItem.radius)],
          geodesic: true,
          bufferSpatialReference: this.view.spatialReference
        }))
        this.buffer = this.buffer[0]
        let areaandlength = await geometryService.areasAndLengths(new AreasAndLengthsParameters({
          calculationType: 'geodesic',
          areaUnit: 'square-meters',
          lengthUnit: 'kilometers',
          polygons: [ this.buffer ]
        }))
        this.buffer.shapearea = areaandlength.areas[0]
      }
      if (this.shapeObj.type === 'polygon') {
        this.buffer = geometryEngine.union([this.buffer, this.shapeObj])
        this.buffer.rings = [this.buffer.rings[0]]
      }
      this.shapearea = this.buffer.shapearea.toFixed(2)
      this.graphic = new Graphic({
        geometry: this.buffer,
        symbol: polySym
      })
      this.map.remove(this.insercLayer)
      await this.view.goTo(this.buffer.extent, { duration: 600 })
      bufferLayer.add(this.graphic)
      this.map.add(bufferLayer)
      this.bufferLayer = bufferLayer
    },
    /**
     *  绘制缓冲区相交
     */
    async clickBtn () {
      let Geoprocessor = await arcgisPackage.Geoprocessor
      let FeatureSet = await arcgisPackage.FeatureSet
      let GraphicLayer = await arcgisPackage.GraphicsLayer
      let geometryEngine = await arcgisPackage.geometryEngine
      let GeometryService = await arcgisPackage.GeometryService
      let AreasAndLengthsParameters = await arcgisPackage.AreasAndLengthsParameters
      // let gp = new Geoprocessor('http://192.168.1.158:6080/arcgis/rest/services/qhhx/intersect/GPServer/intersect')
      let gp = new Geoprocessor(config.GeoprocessorUrl)

      let featureSet = new FeatureSet()

      featureSet.features = [this.graphic]
      /* let params = {
        polygon: featureSet,
        redline: 'wgs'
      } */
      let params = {
        polygon: featureSet,
        areafield: 'mianji',
        // redline: 'E:/' + config.redlineShpUrl
        redline: config.redlineShpUrl
      }
      await gp.submitJob(params).then(result => {
        if (result.jobStatus === 'job-succeeded') {
          gp.getResultData(result.jobId, 'out').then(async res => {
            let polySym = {
              type: 'simple-fill', // autocasts as new SimpleFillSymbol()
              color: [140, 10, 2, 0.7],
              outline: {
                color: [140, 10, 2, 1],
                width: 1
              }
            }
            let geometryService = new GeometryService({
              url: config.geometryServerUrl
            })
            for (let i = 0; i < res.value.features.length; i++) {
              res.value.features[i].symbol = polySym
              // 计算面积
              let areaandlength = await geometryService.areasAndLengths(new AreasAndLengthsParameters({
                calculationType: 'geodesic',
                areaUnit: 'square-meters',
                lengthUnit: 'kilometers',
                polygons: [ res.value.features[i].geometry ]
              }))
              res.value.features[i].attributes.shapearea = areaandlength.areas[0]
            }
            this.comState = true
            this.srldFeature = res.value.features
            this.map.remove(this.insercLayer)
            let insercLayer = new GraphicLayer()
            insercLayer.addMany(res.value.features)
            await this.map.add(insercLayer)
            this.insercLayer = insercLayer
            this.analysisEnd = true
            await setTimeout(() => { this.screenshot() }, 1000)
          })
        } else {
        }
      })
    },
    /**
     * 截图
     */
    screenshot () {
      let mapPointMax = {
        x: this.buffer.extent.xmax,
        y: this.buffer.extent.ymax,
        spatialReference: {
          wkid: config.mapWkid
        }
      }
      let mapPointMin = {
        x: this.buffer.extent.xmin,
        y: this.buffer.extent.ymin,
        spatialReference: {
          wkid: config.mapWkid
        }
      }
      let screenPointmax = this.view.toScreen(mapPointMax)
      let screenPointmin = this.view.toScreen(mapPointMin)
      // screenPoint = MapView.toScreen([this.buffer.extent.xmax, this.buffer.extent.ymax])
      // console.log(screenPoint)
      let area = {
        x: Math.min(screenPointmin.x, screenPointmax.x) - 20,
        y: Math.min(screenPointmin.y, screenPointmax.y) - 20,
        width: Math.abs(screenPointmax.x - screenPointmin.x) + 40,
        height: Math.abs(screenPointmax.y - screenPointmin.y) + 40
      }
      this.proportion = (Math.abs(screenPointmax.y - screenPointmin.y) + 40) / (Math.abs(screenPointmax.x - screenPointmin.x) + 40)
      this.proportion = this.proportion.toString()
      this.view.takeScreenshot({ area: area, format: 'png' })
        .then((screenshot) => {
          this.dataUrl = screenshot.dataUrl
          this.analysisSpin = false
        })
    },
    /**
     * 分析
     */
    analysis () {
      this.comState = false
      // 表单验证
      this.$refs['formValidate'].validate((valid) => {
        if (valid) {
          this.analysisSpin = true
          if (JSON.stringify(this.shapeObj) !== '{}') {
            if (this.shapeObj.type === 'polygon') {
              if (this.view.graphics.items[0].geometry.isSelfIntersecting) {
                // 打开分析报告
                this.validateText = '请绘制闭合且不会自相交的多边形进行分析'
                // 清除图层
                this.view.graphics.removeAll()
                this.drawValidate = true
                this.analysisSpin = false
              } else {
                this.analysisDraw()
              }
            } else {
              this.analysisDraw()
            }
          } else {
            this.validateText = '请先绘制或上传项目形状'
            this.drawValidate = true
            this.analysisList = false
            this.analysisSpin = false
          }
        }
      })
    },
    /**
     * 绘图
     */
    async analysisDraw () {
      this.map.remove(this.bufferLayer)
      this.map.remove(this.insercLayer)
      this.drawValidate = false
      this.analysisList = true
      await this.getbuffer()
      await this.clickBtn()
    },
    /**
     * 定位
     */
    onclickDingwei (index) {
      let graphics = this.insercLayer.graphics.items
      graphics.forEach((graphic, i) => {
        if (i === index) {
          graphic.symbol = {
            type: 'simple-fill', // autocasts as new SimpleFillSymbol()
            color: [12, 140, 2, 0.8],
            outline: {
              color: [0, 0, 0, 0.8],
              width: 2
            }
          }
          this.view.goTo(graphic, { duration: 600 })
        } else {
          graphic.symbol = {
            type: 'simple-fill', // autocasts as new SimpleFillSymbol()
            color: [140, 10, 2, 0.8],
            outline: {
              color: [0, 0, 0, 0.8],
              width: 2
            }
          }
        }
      })
    },
    /**
    * 上传excal坐标文件
    */
    async handleUpload (file) {
      let WebMercatorUtils = await arcgisPackage.WebMercatorUtils
      let param = new FormData()
      param.append('file', file, file.name)
      importFile(param).then((res) => {
        if (res.data.code === '0000') {
          this.excalData = []
          /* for (let i in res.data.data) {
            if (res.data.data[i][0] !== null && res.data.data[i][1] !== null) {
              this.excalData.push(WebMercatorUtils.lngLatToXY(res.data.data[i][0], res.data.data[i][1]))
            }
          } */
          if (res.data.data.length === 0) {
            this.$Modal.warning({
              title: '警告',
              content: '请在模板文件中填充数据'
            })
          } else {
            switch (this.drawType) {
              case 'point':
                if (this.excalData.length > 1) {
                  this.$Modal.warning({
                    title: '警告',
                    content: '当项目形状为点,且excal文件中存在多个点时只显示第一个点'
                  })
                }
                this.renderPoint(res.data.data[0])
                break
              case 'polyline':
                this.renderLine(res.data.data)
                break
              case 'polygon':
                if (this.excalData[0] !== this.excalData[this.excalData.length - 1]) {
                  this.excalData.push(this.excalData[0])
                }
                this.renderPolygon(res.data.data)
                break
            }
            this.view.goTo(this.graphicUp)
          }
        }
      })
    },
    /**
     * 保存判定
     */
    addPro () {
      if (this.addData.result === '落在') {
        this.$Modal.confirm({
          title: '确认',
          content: '项目落在红线区域内，请问确定保存么？',
          onOk: () => {
            this.savePro()
          }
        })
      } else {
        this.savePro()
      }
    },
    /**
    * 保存
    */
    savePro () {
      this.$refs['formValidate'].validate((valid) => {
        if (valid) {
          addPro({
            data: this.addData
          }).then((res) => {
            if (res.data.code === '0000') {

              this.$Notice.success({ title: '保存成功' })
              this.id = res.data.data.id
              this.saveFileUrl = res.data.data.fileUrl
              this.saveEnd = true
              this.$emit('getNewList')
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    /**
     * 获取查看到的数据
     */
    /* 项目准入查看 */
    async getProjectById () {
      await getProjectById({
        data: {
          id: this.seeId
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.formItem.name = res.data.data.name
          // this.formItem.radius = res.data.data.radius.toString()
          this.formItem.description = res.data.data.description
          this.drawType = res.data.data.shape
          this.$set(this.formItem, 'type', res.data.data.type)
          this.$set(this.formItem, 'attribute', res.data.data.attribute)
          this.$set(this.formItem, 'time', new Date(res.data.data.time))
          this.$set(this.formItem, 'radius', res.data.data.radius.toString())
          switch (this.drawType) {
            case 'point':
              this.renderPoint(res.data.data.coordinate)
              break
            case 'polyline':
              this.renderLine(res.data.data.coordinate)
              break
            case 'polygon':
              this.renderPolygon(res.data.data.coordinate)
              break
          }
        }
      }).catch((error) => {
        this.$Notice.error({ title: '服务器错误' })
      })
      await this.analysis()
    },
    /**
     * 初始化组件
     * */
    initCom () {
      this.id = ''
      this.formItem = {}
      this.drawType = 'point'
      this.shapeObj = {}
      this.analysisList = false
      this.$refs['formValidate'].resetFields()
      this.analysisEnd = false
      this.saveEnd = false
      this.drawing = false
      this.map.remove(this.bufferLayer)
      this.map.remove(this.insercLayer)
      this.view.graphics.removeAll()
    }
  }
}
</script>
