<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 150px)" id="ProAppQuery"></QueryBox>
      <div class="queryBtns">
        <Button type="primary" size="small" icon="md-cloud-download" @click="actExcelMdal = true" class="exportBtn">导出</Button>
        <Button type="success" size="small" icon="md-add" @click.stop="addActive = true;$refs.proAppAdd.initCom()" class="btn-add">新建</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table :loading="loading" border stripe :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" :page-size="pageSize" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <div class="add_box" :class="{addActive:addActive}">
      <ProAppAdd ref="proAppAdd" :seeId="seeId" :fileUrl="fileUrl" @getNewList="getNewList" @signOut="signOut"></ProAppAdd>
    </div>
    <!-- 导出文件 -->
    <Modal
      v-model="actMdal"
      title="选择报告格式"
      @on-ok="downFile">
      <div style="text-align: center">
        <RadioGroup v-model="actType">
          <Radio label="0">
            <span>pdf格式</span>
          </Radio>
          <Radio label="1">
            <span>doc格式</span>
          </Radio>
        </RadioGroup>
      </div>
    </Modal>
    <!-- 导出列表 -->
    <Modal
      v-model="actExcelMdal"
      title="选择文件格式"
      @on-ok="handleExport">
      <div style="text-align: center">
        <RadioGroup v-model="actExcelType">
          <Radio label="0">
            <span>excel格式</span>
          </Radio>
          <Radio label="1">
            <span>pdf格式</span>
          </Radio>
        </RadioGroup>
      </div>
    </Modal>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import ProAppAdd from './ProApp_add'
import { list, proDelete, downlloadFileApiUrl, exportWord } from '@/api/projectApproval'
import { renderTime } from '@/utils/common'
export default {
  name: 'ActualTablet',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      /* 表格高度 */
      tableHeight: window.innerHeight - 312,
      /* 表头 */
      columns1: [
        // {
        //   type: 'selection',
        //   width: 60,
        //   align: 'center'
        // },
        {
          title: '序号',
          key: 'jzKh',
          align: 'center',
          minWidth: 100,
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '项目名称',
          key: 'name',
          align: 'center',
          minWidth: 200
        },
        {
          title: '项目时间',
          key: 'time',
          align: 'center',
          width: 120,
          render: (h, params) => {
            return h('span', renderTime(params.row.time))
          },
          sortable: true
        },
        {
          title: '属性',
          key: 'attribute',
          align: 'center',
          minWidth: 150
        },
        {
          title: '类型',
          key: 'type',
          align: 'center',
          minWidth: 150,
          sortable: true
        },
        {
          title: '项目简介',
          key: 'description',
          align: 'center',
          minWidth: 200
        },
        {
          title: '创建时间',
          key: 'createDate',
          align: 'center',
          width: 120,
          render: (h, params) => {
            return h('span', renderTime(params.row.createDate))
          }
        },
        {
          title: '分析结果',
          key: 'result',
          align: 'center',
          width: 120
        },
        {
          title: '分析报告',
          key: 'placeName',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('div', [
              h('Button',
                {
                  props: {
                    type: 'success',
                    size: 'small',
                    class: 'btn-exportBtn',
                    icon: 'md-cloud-download'
                  },
                  on: {
                    click: () => {
                      this.actMdal = true
                      this.actModalData = params.row
                    }
                  }
                },
                '下载'
              )
            ])
          }
        },
        {
          title: '操作',
          key: 'isBs',
          align: 'center',
          width: 170,
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '10px'
                },
                on: {
                  click: () => {
                    this.seeId = params.row.id
                    this.addActive = true
                    this.fileUrl = params.row.fileUrl
                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该项目么？',
                      onOk: () => {
                        this.proDelete(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      /* 表格数据 */
      data1: [],
      /* 页码 */
      pageNum: 1,
      /* 每页条数 */
      pageSize: Math.floor((window.innerHeight - 312) / 48),
      /* 总条数 */
      total: 0,
      /* 加载 */
      loading: false,
      /* 新建框状态 */
      addActive: false,
      /* 查看数据的id */
      seeId: 0,
      fileUrl: '',
      /* 分析报告格式选择 */
      actMdal: false,
      actType: '0',
      actModalData: {},
      /* 导出excal */
      actExcelMdal: false,
      actExcelType: '0'
    }
  },
  components: {
    QueryBox,
    ProAppAdd
  },
  computed: {
    // 查询表单
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'projectName',
          title: '项目名称'
        },
        {
          type: 4,
          field: 'time',
          title: '项目时间'
        },
        {
          type: 1,
          field: 'attribute',
          title: '属性'
        },
        {
          type: 1,
          field: 'type',
          title: '类型'
        }
      ]
    }
  },
  watch: {
    queryData () {
      this.getTableData()
    }
  },
  created () {
    this.getTableData()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableData()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableData()
    },
    /* 获取项目列表 */
    getTableData () {
      this.loading = true
      let temp = this.queryData
      let startTime = ''
      let endTime = ''
      if (temp.time && temp.time.length > 0) {
        startTime = temp.time[0]
        endTime = temp.time[1]
      }
      list({
        data: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          attribute: temp.attribute,
          projectName: temp.projectName,
          type: temp.type,
          startTime: startTime,
          endTime: endTime
        }
      }).then((res) => {
        this.data1 = res.data.data.rows
        this.total = res.data.data.total
        this.loading = false
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 删除 */
    proDelete (id) {
      proDelete({
        data: {
          id: id
        }
      }).then(() => {
        this.$Notice.success({ title: '删除成功' })
        this.getTableData()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (e) {
      this.pageNum = e
      this.getTableData()
    },
    /* 新建页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    },
    /* 新建保存后刷新列表 */
    getNewList () {
      this.getTableData()
      this.pageNum = 1
    },
    /* 导出文件 */
    handleExport () {
      let temp = this.queryData
      let startTime = ''
      let endTime = ''
      if (temp.time && temp.time.length > 0) {
        startTime = temp.time[0]
        endTime = temp.time[1]
      }
      downlloadFileApiUrl({
        data: {
          attribute: temp.attribute,
          projectName: temp.projectName,
          type: temp.type,
          startTime: startTime,
          endTime: endTime
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          let data = res.data.data
          if (!data) {
            return
          }
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = ''
          if (this.actExcelType === '0') {
            url = res.data.data.excelPath
          } else {
            url = res.data.data.pdfPath
          }
          link.href = '/epr' + url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        }
        this.actExcelMdal()
      })
    },
    /* 下载分析报告 */
    downFile () {
      exportWord({
        data: {
          id: this.actModalData.id
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          let data = res.data.data
          if (!data) {
            return
          }
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = ''
          if (this.actType === '0') {
            url = res.data.data.pdfPath
          } else {
            url = res.data.data.wordPath
          }
          link.href = '/epr' + url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        }
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        position: absolute;
        bottom: 0;
        right: 0;
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      margin-right: 10px;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .btn-download {
      background-color: #1ccf20;
      border-color: #1ccf20;
      &:hover, &:focus {
        background-color: #13bb17;
        border-color: #13bb17;
      }
    }
    .add_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
  }
</style>
